import React from "react";
import ReactDOM from "react-dom";
import singleSpaReact from "single-spa-react";
import App from "./App";
var lifecycles = singleSpaReact({
    React: React,
    ReactDOM: ReactDOM,
    rootComponent: App,
    errorBoundary: function () {
        return React.createElement("div", null, "Error loading Comments App");
    }
});
export var bootstrap = lifecycles.bootstrap, mount = lifecycles.mount, unmount = lifecycles.unmount;
// Standalone render
var el = document.getElementById("app");
if (el)
    ReactDOM.render(React.createElement(App, null), el);
